import { Injectable } from '@angular/core';
import { RxRecordInstanceService } from '@helix/platform/record/api';
import { map } from 'lodash';
import { forkJoin, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class RxEmailProfilesService {
  constructor(private rxRecordInstanceService: RxRecordInstanceService) {}

  deleteEmailProfiles(recordDefinitionName: string, recordInstanceIds: string[]): Observable<any[]> {
    return forkJoin(
      map(recordInstanceIds, (id) => {
        return this.rxRecordInstanceService.delete(recordDefinitionName, id);
      })
    );
  }
}
